.ExpertiesContainer {
    /* border: 2px solid red; */
    width: 200vh;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.MySkillsContainer {
    /* border: 2px solid rgb(13, 0, 255); */
    width: 100%;
    height: 75vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}

.skillRange {
    /* border: 2px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0px -130px 0px 24px;
}


.skill {
    /* border: 2px solid green; */
    width: 11rem;
    height: 11rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background: rgb(240, 240, 240);
}

.outer {
    /* border: 2px solid red; */
    width: 80%;
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    box-shadow: 6px 6px 10px -1px rgba(0, 0, 0, .5),
        -6px -6px 10px -1px rgba(255, 255, 255, .7);
}

.inner {
    /* border: 1px solid blue; */
    width: 70%;
    height: 70%;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: inset 4px 4px 6px 2px rgba(0, 0, 0, 0.2);
}

#number {
    text-align: center;
    /* border: 1px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


.skillPercentage {
    color: rgb(115, 115, 115);
}

svg {
    position: relative;
    top: 1.5px;
    left: -167px;
}

@keyframes rotate80 {
    100% {
        stroke-dashoffset: 165;
    }
}

@keyframes rotate78 {
    100% {
        stroke-dashoffset: 190;
    }
}



@keyframes rotate30 {
    100% {
        stroke-dashoffset: 350;
    }
}

@keyframes rotate70 {
    100% {
        stroke-dashoffset: 210;
    }
}

@keyframes rotate60 {
    100% {
        stroke-dashoffset: 240;
    }
}

@keyframes rotate50 {
    100% {
        stroke-dashoffset: 280;
    }
}

@keyframes rotate10 {
    100% {
        stroke-dashoffset: 420;
    }
}

@keyframes rotate20 {
    100% {
        stroke-dashoffset: 390;
    }
}

@keyframes rotate46 {
    100% {
        stroke-dashoffset: 300;
    }
}

@media only screen and (max-width: 1000px) {
    body {
        overflow-x: hidden;
    }

    .ExpertiesContainer {
        /* border: 2px solid red; */
        width: 70vh;
        height: 100%;
    }

    .MySkillsContainer {
        /* border: 2px solid red; */
        height: 100%;
        margin: 10px 0px;
    }

    .skillRange {
        /* border: 2px solid green; */
        margin: 0px -10rem 10px 0rem;
    }

    .skill {
        /* border: 2px solid blue; */
    }
}

@media only screen and (max-width: 650px) {
    body{
        overflow-x: hidden;
    }
    .ExpertiesContainer {
        /* border: 2px solid red; */
        width: 100%;
    }
    .MySkillsContainer{
        /* border: 2px solid blue; */
    }

    .skillRange{
        /* border: 2px solid green; */
    }
}